<template>
  <v-app>
    <v-main class="main-container">
      <router-view/>
      <div class="status_bar">
        <div class="d-flex align-center">
          <h5>Device Status</h5>: <u class="ml-2" :class="socket_status? 'text-green' : 'text-red'"> {{socket_status ? 'Connected' : 'Disconnected'}}</u> 
        </div>
      </div>
    </v-main>
  </v-app>
</template>

<script>
import { mapState } from "vuex";
import config from "./config";
import io from "socket.io-client";
const socket = io(config.url_server());

export default {
  name: 'App',
  computed: {
    ...mapState(['socket_instance']),
  },
  data: () => ({
    socket_status: false,
    //
  }),
  mounted() {
    const { socket_instance } = this;

    socket.on("connect", () => {
      this.socket_status = true;
      console.warn("connected");
    });

    socket.on("disconnect", () => {
      this.socket_status = false;
      console.warn("disconnected");
    });

    this.$store.dispatch('setSocketInstance', socket);
  },
}
</script>
<style>
.status_bar{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40px;
  background-color: #c6bdbd;
  border-top: 1px solid #ccc;
  padding-left: 3%;
  padding-right: 3%;
  display: flex;
  /* justify-content: space-around; */
  align-items: center;
  z-index: 100;
} 
.main-container{
  margin-bottom: 50px;
}
</style>
