<template>
  <v-container fluid>
    <h1>Fish Analysis</h1>
    <v-row>
      <v-col class="" cols="6">
        <img
          class="the_fuckin_image"
          :src="`${url_server}video_feed`"
          crossorigin="anonymous"
        />
        <!-- ref="capturedImage" -->
      </v-col>
      <v-col cols="6">
        <!-- analyzed_image -->
        <img
          class="the_fuckin_image"
          :src="analyzed_image"
          ref="capturedImage"
          crossorigin="anonymous"
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="6" class="d-flex flex-column">
        <h2>Weight:</h2>
        <h1 class="ml-3">{{ live.weight }} g</h1>
        <!-- <h3 class="mb-2">Indicators</h3> -->
        <v-col cols="12" class="d-flex">
          <v-row>
            <v-col
              cols="2"
              v-for="(indicator, i) in live.indicators"
              class="d-flex flex-column align-center px-3 indicator-wrapper"
            >
              <span :class="`text-${indicator ? 'black' : 'grey'}`">
                Indicator {{ i + 1 }}
              </span>
              <div
                class="indicator"
                :class="indicator ? 'active' : 'inactive'"
              ></div>
            </v-col>
          </v-row>
        </v-col>
      </v-col>
      <v-col cols="6" class="d-flex flex-column">
        <h2>Weight:</h2>
        <h1 class="ml-3">{{ captured.weight }} g</h1>
        <!-- <h3 class="mb-2">Indicators</h3> -->
        <v-col cols="12" class="d-flex">
          <v-row>
            <v-col
              cols="2"
              v-for="(indicator, i) in captured.indicators"
              class="d-flex flex-column align-center px-3 indicator-wrapper"
            >
              <span :class="`text-${indicator ? 'black' : 'grey'}`">
                Indicator {{ i + 1 }}
              </span>
              <div
                class="indicator"
                :class="indicator ? 'active' : 'inactive'"
              ></div>
            </v-col>
          </v-row>
        </v-col>
      </v-col>
    </v-row>
    <!-- this button should take us to history -->
    <v-btn
      to="/history"
      size="x-large"
      class="history-btn"
      color="teal"
      icon=" mdi-format-list-bulleted-square"
    ></v-btn>

    <v-dialog
      persistent
      v-model="saveDialog"
      transition="dialog-bottom-transition"
      width="auto"
    >
      <v-card>
        <div class="px-5 pt-4 text-h6">Alert</div>
        <v-card-text>
          <div class="pb-3 pt-2 text-body-2">
            You're leaving without <b>save</b>,<br />
            Would you like to save the last test?
          </div>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn color="red" variant="text" @click="cancel">
            No
          </v-btn>
          <v-btn color="success" variant="text" @click="saveData">
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import axios from "axios";
import config from "../config";
import { mapState } from "vuex";

export default {
  data: () => ({
    saveDialog: false,
    to: null,
    actions: [false, false, false, false],
    live: {
      weight: 0,
      indicators: [],
    },
    captured: {
      weight: 0,
      indicators: [],
    },
    analyzed_image: null,
    capture_state: true,
    analysis_data: null,
    laser_state: false,
  }),
  computed: {
    ...mapState(["socket_instance"]),

    url_server() {
      return config.url_server();
    },

    url_port() {
      return config.url_port();
    },

    filtered_indicators() {
      //return index of True values
      const { indicators } = this.captured;
      return indicators.reduce((acc, value, index) => {
        if (value) acc.push(index);
        return acc;
      }, []);
    },
  },
  methods: {
    toggle_laser() {
      this.laser_state = !this.laser_state;
      this.socket_instance.emit("laser", { state: this.laser_state });
    },

    triggerAction(action) {
      const { tare, setZero, cancel, capture } = config.action_indexes;

      switch (action) {
        case tare:
          this.toggle_laser();
          break;
        case setZero:
          // this.capture();
          break;
        case cancel:
          this.cancel();
          break;
        case capture:
          this.capture();
          break;
      }
    },

    capture() {
      this.captured = JSON.parse(JSON.stringify(this.live));
      if (this.analyzed_image) {
        this.saveData();
      }
      this.putData("capture", {});
      this.capture_state = !this.capture_state;
      this.resetLive();
    },

    cancel() {
      this.saveDialog = false;
      this.resetCaptured();
      this.resetLive();
      this.analyzed_image = null;
    },

    dataURLtoFile(imageElement, filename) {
      const canvas = document.createElement("canvas");
      const context = canvas.getContext("2d");
      canvas.width = imageElement.width;
      canvas.height = imageElement.height;
      context.drawImage(imageElement, 0, 0);

      // Get the data URL of the image from the canvas
      const dataURL = canvas.toDataURL("image/jpeg");

      const arr = dataURL.split(",");
      const mime = arr[0].match(/:(.*?);/)[1];
      const bstr = atob(arr[1]);
      let n = bstr.length;
      const u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, { type: mime });
    },

    saveData() {
      this.saveDialog = false;
      const imageElement = this.$refs.capturedImage;

      const file = this.dataURLtoFile(imageElement, "image.jpg");

      const { analysis_data, filtered_indicators } = this;
      const { weight } = this.captured;

      console.warn(filtered_indicators);

      const formData = new FormData();
      formData.append("defects", JSON.stringify(filtered_indicators));
      formData.append("weight", weight);
      formData.append("length", analysis_data.length);
      formData.append("height", analysis_data.height);
      formData.append("head", analysis_data.head);
      formData.append("tail_trigger", analysis_data.tail_trigger);
      formData.append("image", file); // Append the image data URL

      // console.log("Form Data:");
      // for (const [key, value] of formData.entries()) {
      //   console.log(key, value);
      // }
      this.submitRequest(formData);
    },

    async submitRequest(formData) {
      // const loading = this.$loading();
      axios
        .post(`http://127.0.0.1:${this.url_port}/add`, formData, {
          headers: {
            Accept: "application/json",
            "Content-Type": "multipart/form-data",
          },
        })
        .then(
          (response) => {
            // this.confirm = false;
            // this.reset();
            //console.log(response.data);
            // setTimeout(() => {
            //   loading.close();
            //   window.location.reload();
            // }, 1000);
            // this.$notify({
            //   message: "Información guardada con éxito.",
            //   type: "success"
            // });
          },
          (error) => {
            this.confirm = false;
            console.log(error);
            // setTimeout(() => {
            //   loading.close();
            // }, 1000);
            // this.$notify.error("Ha habido un error.");
          }
        );
    },

    putData(url, options) {
      this.socket_instance.emit(url, options);
      console.warn("putData", url, options);
    },

    resetCaptured() {
      this.captured.indicators.forEach((value, index) => {
        this.captured.indicators[index] = false;
      });
      this.captured.weight = 0;
    },

    resetLive() {
      this.socket_instance.emit("reset_defects", {});
      this.live.indicators.forEach((value, index) => {
        this.live.indicators[index] = false;
      });

      this.live.weight = 0;

      this.actions.forEach((value, index) => {
        this.actions[index] = false;
      });
    },
  },
  beforeRouteLeave(to, from, next) {
    console.warn("a donde puto");
    if (this.analyzed_image) {
      this.to = to;
      this.saveDialog = true;
    } else {
      next();
    }
  },
  mounted() {
    var { noOfDefects } = config;

    this.live.indicators = [...new Array(noOfDefects).fill(false)];
    this.captured.indicators = [...new Array(noOfDefects).fill(false)];

    window.addEventListener("beforeunload", (event) => {
      if (this.analyzed_image) {
        this.saveDialog = true;
        event.preventDefault();
        event.returnValue = "";
      }
    });

    const { socket_instance } = this;
    if (socket_instance.connected) {
      socket_instance.emit("update_indicator_status", {});
    }

    socket_instance.on("indicator_update", (data) => {
      const { keys, keys_to_filter } = config;
      let actions = [];
      let indicators = [];

      data.forEach((value, index) => {
        if (keys_to_filter.some((key) => key == keys[index]))
          actions.push(value);
        else indicators.push(value);
      });

      this.actions = actions;
      this.live.indicators = indicators;
    });

    socket_instance.on("weight_update", (data) => {
      // console.log(data);
      this.live.weight = data;
    });

    socket_instance.on("analysis_data", (data) => {
      const la_merde = JSON.parse(data);
      this.analysis_data = la_merde;
      // console.log(la_merde);
    });

    socket_instance.on("frame_ready", () => {
      this.analyzed_image = `${this.url_server}analyzed_image?${Date.now()}`;
    });
  },
  beforeDestroy() {
    window.removeEventListener("beforeunload", this.handleBeforeUnload);
  },
  watch: {
    actions: {
      handler: function (newVa, oldVal) {
        //spot the one that changed
        let changed;
        newVa.filter((value, index) => {
          if (value != oldVal[index]) changed = index;
        });
        this.triggerAction(changed);
      },
      deep: true,
    },
  },
};
</script>

